	module.exports = {"data":{"site":{"siteMetadata":{"title":"Crazy Woman","subtitle":"Enlouqueça para encontrar a lucidez.","copyright":"© Todos os direitos reservados","menu":[{"label":"Artigos","path":"/"},{"label":"Sobre","path":"/sobre/"},{"label":"Contato","path":"/contato/"}],"author":{"name":"Mariana Brito","facebook":"https://www.facebook.com/briitomari","twitter":"https://twitter.com/briitomari","instagram":"https://www.instagram.com/briitomari/","github":"https://github.com/BritoMari","rss":"#","email":"mariana.bsouza@hotmail.com"}}},"markdownRemark":{"id":"/Users/marianabrito/dev/crazywoman/src/pages/articles/2018-01-05---Orgulho-e-poder-ser-quem-voce-e-num-mundo-que-te-diz-o-contrario/index.md absPath of file >>> MarkdownRemark","html":"<p><img src=\"https://raw.githubusercontent.com/BritoMari/crazywoman/master/src/pages/articles/2018-01-05---orgulho-e-poder-ser-quem-voce-e-num-mundo-que-te-diz-o-contrario/mundo.jpg\" alt=\"Mundo\"></p>\n<p><em>“Para vencer nesse mundo cruel, precisamos passar por poucas e boas.”</em></p>\n<p>Vou direto ao ponto e continuo a discutir mais depois: voltando do centro com minha companheira nessa última terça (02/01/18), em um biarticulado que por si é um ônibus enorme, sentamos no último banco e dava pra ver que havia bastante poltronas vazias, saiu um homem lá da frente e parou praticamente frente a nós e começou a nos encarar, deixei pra lá.</p>\n<p>Sabe aquele ditado de que você tem um <strong>anjo da guarda</strong> todos os dias contigo? Então, um moço, sentado praticamente ao nosso lado perguntou a esse indivíduo o porque de tanto encarar nós duas, ele retrucou e começou a dizer que eu queria me tornar homem e por coincidência um dos meus <a href=\"http://crazywoman.top/posts/eu-nao-quero-me-tornar-um-homem/\">últimos textos</a> foi sobre isso. Eu ia ficar quieta, mas ele começou a nos insultar, a me humilhar, eu retruquei discordando com ele, mas insistiu em continuar, disse também que era melhor do que eu, que iria me bater. Então eu e minha namorada levantamos e fomos pra parte da frente do ônibus, e lá atrás os dois ainda saíram nos tapas, assim por dizer, resumindo… desceram na delegacia e <strong>não deu em nada</strong>.</p>\n<p>O que posso dizer sobre isso tudo?</p>\n<p><strong>Medo!</strong> Essa é a palavra. Medo de sair a noite e topar na rua um indivíduo desse e sofrer coisas piores, dor que senti pelas palavras ditas por ele.</p>\n<p>Quero que você que está lendo, saiba que não escolhi sofrer preconceito, não é uma escolha amar uma pessoa que <strong>aos olhos de muitos é errado</strong>, quero que saiba que não é porque eu namoro uma pessoa do mesmo sexo que eu <strong>queira mudar de sexo</strong>. Não é fácil ser olhada por muitos como a diferente, a esquisita, pessoas sofrem todos os dias, por ser gordo, magro, gay, lésbica, negro e por aí vai, não é uma escolha e sim o que a vida nos deu e você não tem direito nenhum de opinar ou humilhar a vida e escolha de outra pessoa.</p>\n<p>Ouvir palavras duras de um parente, até vai, aguentamos, mas ouvir de alguém que eu nunca sequer vi na vida é difícil. Se você passa a sofrer isso todos os dias, acho que você acaba acreditando, digerindo isso e mudando o jeito de ser, já li muitos depoimentos que pessoas desistem de ser o que realmente são por simplesmente não aguentar mais ser a <em>“diferente”</em>.</p>\n<p>Perguntei pra minha companheira, o que ele ganharia com isso? Ela disse: História pra contar aos amigos que é o bonzão.</p>\n<p>Eu acordo todos os dias com esperança de que esse mundo pode ser melhor, com menos ódio, rancor e medo no olhar das pessoas, sonho um dia que tudo que eu toque tenha amor, só depende das pessoas, se temos a capacidade de odiar pode ter certeza que <strong>temos capacidade de amar ainda mais</strong> e deixar o ódio e preconceito de lado.</p>","fields":{"tagSlugs":["/tags/amor/","/tags/orgulho/","/tags/medo/"]},"frontmatter":{"title":"Orgulho é poder ser quem você é num mundo que te diz o contrário","tags":["Amor","Orgulho","Medo"],"date":"2018-01-05T13:00:14.138Z","description":"Para vencer nesse mundo cruel, precisamos passar por poucas e boas."}}},"pathContext":{"slug":"/posts/orgulho-e-poder-ser-quem-voce-e-num-mundo-que-te-diz-o-contrario/"}}

